    align-items: center;
    padding:0px 24px;
    gap: 232px;

    position: absolute; 
    width: 375px;
    height: 80px;
    left: 0px;
    top: 0px;

    /* Neutral color/800 */

    background: #0F1017;

    }
    .logo{
    width: 153px;
    height: 30px;
    
    
    /* Inside auto layout */
    
    flex: none;
    order: 0;
    flex-grow: 0;
    }    
    .logo1{
